.NotFound{
    min-height: 71vh;
    vertical-align: center;
    text-align: center;
    margin-top: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-items: center;

    h4{
        font-size: 2.5rem;
        color: #ff6a6a;
    }
    span{
        font-size: 1rem;
        color: #ff6a6acc;
    }
    
    img{
        height: 94px;
        width: 75px;
    }
}



@media (min-width:1024px){
    .NotFound{
        margin-bottom: 45px;
        h4{
            font-size: 5rem;
            color: #ff6a6a;
        }
        span{
            font-size: 2rem;
            color: #ff6a6acc;
        }

        img{
            height: 188px;
            width: 150px;
        }

    }

   
}